import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'filter'
})
export class FilterPipe implements PipeTransform {

  transform(users: any, term: any, newOnly: any): any {

    // If flag is true, return only new customers
    if(newOnly){
      users = users.filter((user) => {
        return user.hasRoles.includes('newCustomer');
      });
    }

    // Check if undefined
    if (term === undefined) {
      return users;
    }

    // Else return updated cleanArray
    return users.filter((user) => {
      return user.name.concat(user.email).toLowerCase().includes(term.toLowerCase());
    })
  }

}



