import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { ValidateService } from '../../services/validate.service';
import { AuthService } from '../../services/auth.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import { Router, ActivatedRoute, Params } from '@angular/router';

@Component({
  selector: 'app-edit-profile',
  templateUrl: './edit-profile.component.html',
  styleUrls: ['./edit-profile.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class EditProfileComponent implements OnInit {

  _id: String;
  name: String;
  email: String;
  address: String;
  oldProfile: {_id: String, name: String, email: String, address: String};

  constructor(
    private validateService: ValidateService,
    private flashMessage: FlashMessagesService,
    private authService: AuthService,
    private route: ActivatedRoute,
    private router: Router) { }

  ngOnInit() {
    this._id = this.route.snapshot.params.id;

    this.authService.getProfile().subscribe(profile => {

      this.oldProfile = profile.user;
      if(this._id != this.oldProfile._id) {
        this.flashMessage.show('You are not allowed to do that.', {cssClass: 'alert-danger', timeout: 3000});
        this.router.navigate(['/profile']);
      }
    },
    err => {
      this.flashMessage.show('Something went wrong.', {cssClass: 'alert-danger', timeout: 3000});
      this.router.navigate(['/profile']);
    });
  }

  onEditSubmit(){

    const user = {
      name: this.name,
      email: this.email,
      address: this.address
    }

    // Required Fields
    if(!this.validateService.validateEditProfile(user)){
      this.flashMessage.show('Please fill in required fields.', {cssClass: 'alert-danger', timeout: 3000});
      return false;
    }

    // Validate Email
    if(!this.validateService.validateEmail(user.email)){
      this.flashMessage.show('Please use a valid email.', {cssClass: 'alert-danger', timeout: 3000});
      return false;
    }

    // Edit User Profile
    this.authService.editUserProfile(user, this._id).subscribe(data =>{
      if(data.success){
        this.flashMessage.show('You have successfully edited your profile.', {cssClass: 'alert-success', timeout: 3000});
        this.router.navigate(['/profile']);
      } else {
        this.flashMessage.show(data.msg, {cssClass: 'alert-danger', timeout: 3000});
      }
  });
  }
}



