import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { ValidateService } from '../../services/validate.service';
import { AuthService } from '../../services/auth.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import { Router, ActivatedRoute, Params } from '@angular/router';

@Component({
  selector: 'app-customer-profile',
  templateUrl: './customer-profile.component.html',
  styleUrls: ['./customer-profile.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class CustomerProfileComponent implements OnInit {
  _id: String;
  user: {name: String, email: String, address: String, hasRoles: [String]};

  constructor(
    private validateService: ValidateService,
    private flashMessage: FlashMessagesService,
    private authService: AuthService,
    private route: ActivatedRoute,
    private router: Router
  ) { }

  ngOnInit() {
    this._id = this.route.snapshot.params.id;

    this.authService.getUserProfile(this._id).subscribe(profile => {
      if(!profile.success  || !this.authService.checkRole('isStaff')) {

        this.flashMessage.show('You are not authorized to view this.', {cssClass: 'alert-danger', timeout: 3000});
        this.router.navigate(['/']);
      } else {

        this.user = profile.user;
      }
    },
    err => {
      this.flashMessage.show('Something went wrong.', {cssClass: 'alert-danger', timeout: 3000});
      this.router.navigate(['/customers']);
    });
  }

  toggleBuy(){

    this.authService.toggleBuy(this.user).subscribe(data =>{
      if(data.success){

        this.user = data.user;
        let msg = "";
        if(this.user.hasRoles.includes('notApproved')){
          msg = "Customer can no longer purchase products.";
        } else {
          msg = "Customer can now purchase products!";
          //Add authService method to send to invoice micro
        }
        this.flashMessage.show(msg, {cssClass: 'alert-success', timeout: 3000});
      } else {
        this.flashMessage.show('Something went wrong. Please try again later.', {cssClass: 'alert-danger', timeout: 3000});
      }
    });
  }
}



