import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { RouterModule, Routes } from '@angular/router';
import { FlashMessagesModule } from 'angular2-flash-messages';
import { AuthGuard } from './guards/auth.guard';
import { JwtHelper } from 'angular2-jwt';

import { AppComponent } from './app.component';
import { NavbarComponent } from './components/navbar/navbar.component';
import { LoginComponent } from './components/login/login.component';
import { RegisterComponent } from './components/register/register.component';
import { HomeComponent } from './components/home/home.component';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { ProfileComponent } from './components/profile/profile.component';
import { EditProfileComponent } from './components/edit-profile/edit-profile.component';
import { CustomerProfileComponent } from './components/customer-profile/customer-profile.component';
import { StaffProfileComponent } from './components/staff-profile/staff-profile.component';
import { CustomersComponent } from './components/customers/customers.component';
import { StaffComponent } from './components/staff/staff.component';

import { ValidateService } from './services/validate.service';
import { AuthService } from './services/auth.service';
import { FilterPipe } from './pipes/filter.pipe';

const appRoutes: Routes = [
  {path:'', component: HomeComponent},
  {path:'register', component: RegisterComponent},
  {path:'login', component: LoginComponent},
  {path:'dashboard', component: DashboardComponent, canActivate:[AuthGuard]},
  {path:'profile', component: ProfileComponent, canActivate:[AuthGuard]},
  {path:'customers/:id', component: CustomerProfileComponent, canActivate:[AuthGuard]},
  {path:'profile/edit/:id', component: EditProfileComponent, canActivate:[AuthGuard]},
  {path:'customers', component: CustomersComponent, canActivate:[AuthGuard]},
  {path:'staff', component: StaffComponent, canActivate:[AuthGuard]},
  {path:'staff/:id', component: StaffProfileComponent, canActivate:[AuthGuard]}
]

@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    LoginComponent,
    RegisterComponent,
    HomeComponent,
    DashboardComponent,
    ProfileComponent,
    CustomersComponent,
    StaffComponent,
    FilterPipe,
    EditProfileComponent,
    CustomerProfileComponent,
    StaffProfileComponent
  ],
  imports: [
    BrowserModule,
    HttpModule,
    FormsModule,
    RouterModule.forRoot(appRoutes),
    FlashMessagesModule
  ],
  providers: [
    ValidateService,
    AuthService,
    AuthGuard,
    JwtHelper
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



