import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { AuthService } from '../../services/auth.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import { Router } from '@angular/router';

@Component({
  selector: 'app-customers',
  templateUrl: './customers.component.html',
  styleUrls: ['./customers.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class CustomersComponent implements OnInit {
  users: Object;
  newOnly: Boolean;

  constructor(
    private authService: AuthService,
    private flashMessage: FlashMessagesService,
    private router: Router
  ) { }

  ngOnInit() {
    this.newOnly = false;

    this.authService.getCustomers().subscribe(customers => {
      this.users = customers.users;
    },
    err => {
      this.flashMessage.show('You are not authorized to view this.', {cssClass: 'alert-danger', timeout: 3000});
      this.router.navigate(['/']);
    });
  }

  viewProfile(user){
    this.router.navigate(['/customers/', user._id]);
  }

  toggleNewCustomers(){
    this.newOnly = !this.newOnly;
  }
}



