import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { ValidateService } from '../../services/validate.service';
import { AuthService } from '../../services/auth.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import { Router, ActivatedRoute, Params } from '@angular/router';

@Component({
  selector: 'app-staff-profile',
  templateUrl: './staff-profile.component.html',
  styleUrls: ['./staff-profile.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class StaffProfileComponent implements OnInit {
  _id: String;
  canMessage: Boolean;
  canBuy: Boolean;
  user: {name: String, email: String, address: String, hasRoles: [String]};

  constructor(
    private validateService: ValidateService,
    private flashMessage: FlashMessagesService,
    private authService: AuthService,
    private route: ActivatedRoute,
    private router: Router
  ) { }

  ngOnInit() {
    this._id = this.route.snapshot.params.id;
    this.canMessage = false;
    this.canBuy = false;

    this.authService.getUserProfile(this._id).subscribe(profile => {
      if(!profile.success || !this.authService.checkRole('isManagement')) {

        this.flashMessage.show('You are not authorized to view this.', {cssClass: 'alert-danger', timeout: 3000});
        this.router.navigate(['/']);
      } else {

        this.user = profile.user;

        if(this.user.hasRoles.includes('canMessage')){
          this.canMessage = true;
        }

        if(this.user.hasRoles.includes('canBuy')){
          this.canBuy = true;
        }
      }
    },
    err => {
      this.flashMessage.show('Something went wrong.', {cssClass: 'alert-danger', timeout: 3000});
      this.router.navigate(['/staff']);
    });
  }

  saveChanges(){

    let hasRoles = [];

    if(this.canMessage){
      hasRoles.push('canMessage');
    }

    if(this.canBuy){
      hasRoles.push('canBuy');
    }

    this.authService.editPermissions(({hasRoles: hasRoles}), this._id).subscribe(data =>{
      if(data.success){
        this.flashMessage.show("Permissions updated successfully!", {cssClass: 'alert-success', timeout: 3000});
      } else {
        this.flashMessage.show('Something went wrong. Please try again later.', {cssClass: 'alert-danger', timeout: 3000});
      }
    });
  }

}



