import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import 'rxjs/add/operator/map';
import { tokenNotExpired } from 'angular2-jwt';
import { JwtHelper } from 'angular2-jwt';

@Injectable()
export class AuthService {
  authToken: any;
  hasRoles: any;

  constructor(
    private http: Http,
    private jwtHelper: JwtHelper
  ) { }

  getWithAuth(url){
    let headers = new Headers();
    this.loadToken();
    headers.append('Authorization', this.authToken);
    headers.append('Content-Type', 'application/json');
    return this.http.get(url, {headers: headers})
      .map(res => res.json());
  }

  putWithAuth(body, url){

    let headers = new Headers();
    headers.append('Authorization', this.authToken);
    headers.append('Content-Type', 'application/json');
    return this.http.put(url, body, {headers: headers})
      .map(res => res.json());
  }

  registerUser(user){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.post('users/register', user, {headers: headers})
      .map(res => res.json());
  }

  editUserProfile(user, _id){
    return this.putWithAuth(user, 'users/profile/edit/' + _id);
  }

  toggleBuy(user){
    return this.putWithAuth(user, 'users/profile/' + user._id + '/toggleBuy');
  }

  editPermissions(hasRoles, _id){
    return this.putWithAuth(hasRoles, 'users/profile/' + _id + '/editPermissions');
  }

  authenticateUser(user){
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.post('users/authenticate', user, {headers: headers})
      .map(res => res.json());
  }

  getProfile(){
    return this.getWithAuth('users/profile');
  }

  getUserProfile(_id){
    return this.getWithAuth('users/profile/' + _id);
  }

  getCustomers(){
    return this.getWithAuth('users/show/customers');
  }

  getStaff(){
      return this.getWithAuth('users/show/staff');
  }

  loadToken(){
    const token = localStorage.getItem('token');
    this.authToken = token;
  }

  checkRole(role){

    var token = localStorage.getItem('token');
    if(token && tokenNotExpired()){

      var hasRoles = this.jwtHelper.decodeToken(token).payload.user.hasRoles;
      if(hasRoles.includes(role)){

        return true;
      }
    }
    return false;
  }

  storeUserData(token){
    localStorage.clear();
    localStorage.setItem('token', token);
  }

  loggedIn(){
    return tokenNotExpired();
  }

  logout(){
    this.authToken = null;
    localStorage.clear();
  }
}



